.header__logo {
  font-family: Playfair Display;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 54px;
  
  color: #FFFFFF;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-decoration: none;
  transition: .3s;
}

.header__logo:hover {
  opacity: .8;
}